import { Answer } from '../types';

export const trail = (
  name: string,
  data?: { [key: string]: string | Answer }
) => {
  if (sensitiveKeys.includes(name)) {
    return;
  }
  const payload =
    data &&
    Object.fromEntries(Object.entries(data).map(clean));
  // @ts-ignore
  if (!window || !window.splitbee) {
    // eslint-disable-next-line no-console
    console.log('No splitbee');
  }
  // @ts-ignore
  // eslint-disable-next-line no-console
  console.log('track', name, payload);
  // eslint-disable-next-line
  //window.splitbee.track(name, data);
};

const clean = ([ key, value ]): string[]  => {
  if (sensitiveKeys.includes(key)) {
    return [key, '---']
  }
  if (typeof value === 'string') {
    return [key, value];
  }
  if (!value) {
    return [key, null];
  }
  if (value instanceof Array) {
    return [key, value.map((v) => v.value).join(',')];
  }
  return [key, value.value];
};

const sensitiveKeys = ['bucketName', 'forwardingBucket']
